/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useContext } from "react";
import { Table, Form } from "@themesberg/react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";
import { Card, Button } from "@themesberg/react-bootstrap";
import { securedAxiosInstance } from "../../axiosConfig";
import { useHistory } from "react-router-dom";
import styles from "../../css/AllCompanies.module.css";
import Loader from "../../components/Loader";
import { AuthContext } from "../../context/AuthContext";

export const PageTrafficTable = ({
  companies,
  searchState,
  companyName,
  eventType,
}) => {
  const TableRow = (props) => {
    const { sourceIcon, sourceIconColor, searchState } = props;
    const history = useHistory();

    return (
      <>
        {companies.map((company, id) => {
          if (
            (searchState === "" ||
              (company.title &&
                company.title
                  .toLowerCase()
                  .includes(searchState.toLowerCase())) ||
              (company.company &&
                company.company.Name.toLowerCase().includes(
                  searchState.toLowerCase()
                ))) &&
            (eventType === "All" || company.type === eventType)
          ) {
            return (
              <tr key={id}>
                <td>
                  <Card.Link href="#" className="text-primary fw-bold">
                    {id + 1}
                  </Card.Link>
                </td>
                <td
                  onClick={() => history.push(`/companies/${company.id}`)}
                  className="fw-bold"
                  style={{
                    cursor: "pointer",
                    color: "#0089ff",
                    textTransform: "capitalize",
                  }}
                >
                  <FontAwesomeIcon
                    icon={sourceIcon}
                    className={`icon icon-xs text-${sourceIconColor} w-30`}
                  />
                  {company.jobtitle}
                </td>
                <td>{company.type}</td>
                <td>{company.company ? company.company.Name : ""}</td>
                <td>{company.placeOfPosting}</td>
                <td>{company.status ? company.status : "--"}</td>
              </tr>
            );
          } else return null;
        })}
      </>
    );
  };

  return (
    <Card
      border="light"
      className="shadow-sm mb-4"
      style={{ borderRadius: "0 0 0.5rem 0.5rem", border: "none" }}
    >
      <Card.Body className={styles.tableWrapper}>
        <Table responsive className="table-centered table-nowrap rounded mb-0">
          <thead className="thead-light">
            <tr>
              <th className="border-0">#</th>
              <th className="border-0">Job Profile</th>
              <th className="border-0">Type</th>
              <th className="border-0">Company</th>
              <th className="border-0">Location</th>
              <th className="border-0">Status</th>
            </tr>
          </thead>
          <tbody>
            <TableRow
              searchState={searchState}
              companyName={companyName}
              eventType={eventType}
            />
          </tbody>
        </Table>
      </Card.Body>
    </Card>
  );
};

const Allcompanies = () => {
  const {
    state: { profileData },
  } = useContext(AuthContext);

  const degree = profileData?.degree;
  const campus = profileData?.campus;
  const batch = profileData?.batch;

  const [companies, setcompanies] = useState([
    { loading: false, data: [], error: null },
  ]);
  const [showMore, setShowMore] = useState(true);
  const [start, setStart] = useState(0);
  const [initialLoader, setInitialLoader] = useState(true);
  const [searchQuery, setSearchQuery] = useState("");
  const [companySelected, setCompanySelected] = useState("All");
  const [eventTypeSelected, setEventType] = useState("All");
  const limit = 20;

  useEffect(() => {
    fetchAllCompanies();
  }, []);

  const fetchAllCompanies = async () => {
    try {
      setcompanies({ ...companies, loading: true });
      const companiesRes = await securedAxiosInstance.get(
        `/company-events?_sort=createdAt:desc&batch=${batch}&_where[0][degrees_allowed_contains]=${degree}&campus=${campus}&_start=${start}&_limit=${limit}`
      );
      let newcompanies = companiesRes.data;

      if (companiesRes.data.length <= limit) {
        setShowMore(false);
      }
      setcompanies({ loading: false, data: newcompanies, error: null });

      setStart(start + limit);
      setInitialLoader(false);
    } catch (error) {
      setcompanies({ loading: false, data: [], error: error });
      console.log(error);
      setInitialLoader(false);
    }
  };

  function show_companies({ searchQuery, companySelected, eventTypeSelected }) {
    if (companies.data) {
      return (
        <PageTrafficTable
          companies={companies.data}
          searchState={searchQuery}
          companyName={companySelected}
          eventType={eventTypeSelected}
        />
      );
    }
  }

  function show_loader() {
    if (companies.loading) {
      return <Loader />;
    }
  }

  function show_button() {
    if (showMore) {
      return (
        <div className="d-flex justify-content-center mt-4">
          <Button
            variant="dark"
            style={{
              background: "linear-gradient(to right, #fb7b4a, #ee3344)",
              borderColor: "#fb7b4a",
            }}
            disabled={companies.loading}
            onClick={() => fetchAllCompanies()}
          >
            {companies.loading ? <Loader /> : "View More"}
          </Button>
        </div>
      );
    }
  }

  return (
    <>
      <div style={{ background: "white", padding: "24px" }}>
        <h2 style={{ color: "black" }}>Job Profiles</h2>
        <div className="p-0 mt-4 bg-white rounded">
          <Form
            className="d-md-flex p-0 align-items-center "
            style={{ gap: "35px" }}
          >
            <div
              className="col-12 col-md-4 mb-2 mb-md-0"
              style={{
                border: "0.0625rem solid #d1d7e0",
                borderRadius: "10px",
              }}
            >
              <Form.Group
                className="d-flex justify-content-center align-items-center"
                style={{ padding: "0 0.75rem" }}
              >
                <Form.Label
                  className="mb-0 d-flex justify-content-center align-items-center"
                  style={{ fontSize: "13px" }}
                >
                  <FontAwesomeIcon
                    icon={faSearch}
                    style={{ marginRight: "5px" }}
                  />
                  Search
                </Form.Label>
                <Form.Control
                  placeholder="Start typing company name..."
                  value={searchQuery}
                  style={{
                    border: "none",
                    marginLeft: "10px",
                    marginRight: "10px",
                    boxShadow: "none",
                    fontStyle: "italic",
                  }}
                  onChange={(event) => {
                    setSearchQuery(event.target.value);
                  }}
                ></Form.Control>
              </Form.Group>
            </div>
            <div
              className="col-12 col-md-4 mb-2 mb-md-0 d-flex justify-content-center align-items-center"
              style={{
                border: "0.0625rem solid #d1d7e0",
                padding: "0 0.75rem",
                paddingRight: "0px",
                borderRadius: "10px",
              }}
            >
              <Form.Label
                className="mb-0"
                style={{ marginRight: "10px", fontSize: "13px" }}
              >
                Type
              </Form.Label>
              <Form.Select
                style={{
                  border: "none",
                  boxShadow: "none",
                  fontStyle: "italic",
                }}
                value={eventTypeSelected}
                onChange={(option) => setEventType(option.target.value)}
              >
                <option>All</option>
                <option>Internship</option>
                <option>Placement</option>
              </Form.Select>
            </div>
            <Button
              className="col-4 col-md-1 mb-2 mb-md-0"
              style={{
                height: "35px",
                background: "linear-gradient(to right, #fb7b4a, #ee3344)",
                borderColor: "#fb7b4a",
                padding: 0,
              }}
              onClick={() => {
                setCompanySelected("All");
                setEventType("All");
                setSearchQuery("");
              }}
            >
              Clear
            </Button>
          </Form>
        </div>
      </div>
      {initialLoader ? (
        <div>
          <Loader />
        </div>
      ) : (
        <>
          {show_companies({ searchQuery, companySelected, eventTypeSelected })}
          {show_loader()}
          {show_button()}
        </>
      )}
    </>
  );
};

export default Allcompanies;
